
<template>
  <div id="manager">
    <div id="title"></div>
    <el-table
      :data="arr"
      style="width: 100%">

      <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">

          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>


      <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">

          <span style="margin-left: 10px">{{ scope.row.password }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="passedit(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>

</template>



<script>
  export default {
    data(){
      return {
        arr:[]
      }
    },
    created(){
      this.$http.get('/api/admin/manager/display').then(res=>{
        this.arr=res.body;
//        console.log(this.arr);
      })
    },
    methods: {
      passedit(id){
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d{3,8}$/,
          inputErrorMessage: ''
        }).then(({value}) => {
          let obj = {}
          obj.pass = value
          obj.id = id
          this.$http.post('/api/admin/manager/pass',obj,{
            headers: {
              "content-type": 'application/json'
            }
          }).then(res => {
//            console.log(res);
            if(res.body.affectedRows===1){
              this.$message({
                type: 'success',
                message: '',
              });
              this.$router.push('/login')
            }else{
              this.$message({
                type: 'error',
                message: '',
              });
            }
          })


        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    }
  }
</script>

<style scoped lang="scss">
#manager{
  width: 100%;
  height: 100%;
}
</style>



