<template>
  <div id="nutmod">
    <div id="title">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/nut' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="name">
        <el-col :span="11">
          <el-input v-model="ruleForm.name"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="price">
        <el-col :span="11">
          <el-input v-model="ruleForm.price"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="count">
        <el-col :span="11">
          <el-input v-model="ruleForm.count"></el-input>
        </el-col>
      </el-form-item>

      <el-form-item label="" prop="fid">
        <el-select v-model="ruleForm.fid" placeholder="">
          <el-option :label="item.name" :value="item.id" v-for="item in fenlei" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="time">
            <el-date-picker type="date" placeholder="" v-model="ruleForm.time"
                            style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" prop="rid">
        <el-checkbox-group v-model="ruleForm.rid">
          <el-checkbox :label="item.id" name="type" v-for="item in recom" :key="item.id">{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="" prop="contfree">
        <el-radio-group v-model="ruleForm.contfree">
          <el-radio label="1"></el-radio>
          <el-radio label="0"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="des">
        <el-input type="textarea" v-model="ruleForm.des"></el-input>
      </el-form-item>

      <el-form-item label="" prop="img">
        <el-upload
          class="upload-demo"
          action="/api/admin/goods/upload"
          multiple
          :on-success="handleSuccess"
          :on-remove="handleRemove"
          :file-list="ruleForm.img"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
        </el-upload>

      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>

</template>
<script>
  export default{
    name: 'nutmod',
    data(){
      return {
        ruleForm: {
          name: '',
          price: '',
          count: '',
          fid: '',
          time: '',
          rid: [],
          contfree: '',
          des: '',
          img: [],
        },

        fenlei: [],
        recom: []
        ,
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 100, message: ' 1  100 ', trigger: 'blur'}
          ],
          price: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 5, message: ' 1  5 ', trigger: 'blur'}
          ],
          count: [
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 5, message: ' 1  5 ', trigger: 'blur'}
          ],
          fid: [
            {required: true, message: '', trigger: 'change'}
          ],
          time: [
            {required: true, message: '', trigger: 'change'}
          ],
          rid: [
            {type: 'array', trigger: 'change'}
          ],
          contfree: [
            {required: true, message: '', trigger: 'change'}
          ],
          des: [
            {required: true, message: '', trigger: 'blur'}
          ]
        }
      }
    }
    ,
    methods: {
      validate(bool, obj)
      {
        console.log(bool, obj)
      },
      submitForm(formName)
      {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let id = this.$route.query.id
            let obj = Object.assign({}, this.ruleForm)
            obj.img = JSON.stringify(obj.img)
            obj.rid=JSON.stringify(obj.rid)
            obj.time = JSON.stringify(obj.time).slice(1,11)
            this.$http.post('/api/admin/goods/upd?id='+id, obj, {
              headers: {
                "content-type": "application/json"
              }
            }).then((res) =>{
              if(res.body=='ok'){
                this.$message({
                  message: '',
                  type: 'success'
                });
              }else{
                this.$message.error('');
              }
            })
          } else {
            this.$message({
              message: '',
              type: 'warning'
            });
            return false;
          }
        });

      }
      ,
      resetForm(formName)
      {
        this.$refs[formName].resetFields();
      },

      handleRemove(file, filelist) {
        this.ruleForm.img = filelist;
      },
      handleSuccess(res, file, filelist) {
//        console.log(filelist);
        this.ruleForm.img = filelist;
      }
    }
    ,
    created()
    {
      let id = this.$route.query.id
      this.$http.get('/api/admin/goods/nutmod?id=' + id).then(res => {
//        console.log(res);
        let body = res.body[0]
        if(body.rid) {
          body.rid = JSON.parse(body.rid)
          if (body.img) {
            body.img = JSON.parse(body.img)
          }
        }

        this.ruleForm = body
//        console.log(this.ruleForm);
      })
      this.$http.get('/api/admin/goods/nutfenlei').then(res => {
//        console.log(res);
        this.fenlei = res.body


      })
      this.$http.get('/api/admin/goods/nutrecom').then(res => {
//        console.log(res);
        this.recom = res.body

      })
    }

  }
</script>
<style scoped lang='scss'>
  #nutmod {
    #title {
      padding: 0 10px 35px;
      margin-top: 0;
      font-size: 20px;
      font-weight: 600;
    }
    .upload-demo {
      width: 400px;
      overflow: hidden;
    }
  }
</style>



